import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {
	constructor(private http: HttpClient) { }

	searchStr: string =''
	length:number = 3
	isLoaded: boolean = false
	val:string=''
	response:any

	handleChange()
	{
		this.http.get('https://api.github.com/users/'+this.searchStr+'?client_id=c82fbd2c4d4c6cc750a0&client_secret=f1060cb0aa945b55dc815a75227f052dfcb62266').subscribe((response)=>{
			this.response=response;
			console.log(this.response)
			if(this.response.avatar_url==null)
			{
				this.isLoaded=false;
			}
			else
			{
				this.isLoaded=true;
			}
		})
	}
  

  ngOnInit() {
  }

}
